export default {
	data() {
		return {
			searchValue:'',
			exist:true,
			productList:[],
			tabBars: [{
							id:'',
							label:'全部',
						}],
			tabIndex: 0,
			classifyId:'',
			pageNum:''
		};
	},
	onLoad(options) {
		this.getClassifyData(this.communityId)
	},
	computed: {
		communityId(){
			return this.$store.getters.communityId
		}
	},
	methods: {
		ontabtap(e) {
			this.tabIndex = e.target.dataset.current || e.currentTarget.dataset.current;
			this.classifyId = this.tabBars[this.tabIndex].id
			this.getProduct(1,'')
		},
		goDetail(id,type){
			uni.navigateTo({
				url: `/pages/health-product-detail/health-product-detail?id=${id}&type=${type}`,
			});
		},
		getClassifyData(id){
			this.$api.getClassify({
				communityId:id,
			}).then(res=>{
				let arr = res.data
				if(res.status=="OK"){
					arr.map(item=>{
						this.tabBars.push({
							id:item.id,
							label:item.name,
						})
					})
					this.classifyId = this.tabBars[0].id
					this.getProduct(1,'')
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		getProduct(pageNum,searchValue){
			this.$api.customerProductPage({
				name:searchValue,
				size:10,
				page:pageNum,
				communityId:this.communityId,
				classifyId:this.classifyId
			}).then(res=>{
				if(res.status=="OK"){
					res.list.map(item=>{
						this.productList.push({
							price:item.price/100,
							originalPrice:item.originPrice/100,
							name:item.name,
							pic:JSON.parse(item.mainPic)&&JSON.parse(item.mainPic)[0]&&JSON.parse(item.mainPic)[0].url,
							id:item.id,
							code:item.code
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		search(res){
			this.getProduct(1,res.value)
		}
		
	},
	filters: {
		toFixed2: function(value) {
			return value.toFixed(2);
		},
	}
	
}
